


import React from 'react';
import {Card, Table, Radio, Row, Col, Button, Pagination} from 'antd';
import moment from 'moment';
import SearchBox from '../../components/searchBox/searchBox'
import ExportFileCom from '../../components/exportFile/exportFile'

import {getFun} from '../../utils/api'
import {objectToArr, dateDiff, milliFormat} from '../../utils/dataHandle'
import './operating.less'

const RadioButton = Radio.Button;
const RadioGroup = Radio.Group;

class Distribution extends React.Component{
    constructor(props) {
        super(props);
        this.state={
            title: '',
            total: 10,
            pageSize: 10,
            current: 1,
            load: true,
            dayNum: 10,
            tableData: [],
            selectValue: '',

            orderOptions: { //
                product_type_id: '',
                order_source: '',
                user_level_id: '',
                user_active_type: ''
            },
            city: '',
            start_at: '',
            end_at: '',
            group_by: 'product_type_id',
            searchParams: {},
            xScroll: '100%',
            tableHeader: [],
            tableHeader0: [
                {
                    title: '', dataIndex: 'start_time', key: 'start_time'
                },
                {
                    title: 'ASAP',
                    children: [
                        {title: '', dataIndex: 'total_of_finished_orders_asap', key: 'total_of_finished_orders_asap'},
                        {title: '', dataIndex: 'order_origin_amount_asap', key: 'order_origin_amount_asap'}
                    ]
                },
                {
                    title: '',
                    children: [
                        {title: '', dataIndex: 'total_of_finished_orders_reservation', key: 'total_of_finished_orders_reservation'},
                        {title: '', dataIndex: 'order_origin_amount_reservation', key: 'order_origin_amount_reservation'}
                    ]
                },
                {
                    title: '-',
                    children: [
                        {title: '', dataIndex: 'total_of_finished_orders_airport_pick_up', key: 'total_of_finished_orders_airport_pick_up'},
                        {title: '', dataIndex: 'order_origin_amount_airport_pick_up', key: 'order_origin_amount_airport_pick_up'}
                    ]
                },
                {
                    title: '-',
                    children: [
                        {title: '', dataIndex: 'total_of_finished_orders_airport_drop_off', key: 'total_of_finished_orders_airport_drop_off'},
                        {title: '', dataIndex: 'order_origin_amount_airport_drop_off', key: 'order_origin_amount_airport_drop_off'}
                    ]
                },
                {
                    title: '-',
                    children: [
                        {title: '', dataIndex: 'total_of_finished_orders_half_day_rent', key: 'total_of_finished_orders_half_day_rent'},
                        {title: '', dataIndex: 'order_origin_amount_half_day_rent', key: 'order_origin_amount_half_day_rent'}
                    ]
                },
                {
                    title: '-',
                    children: [
                        {title: '', dataIndex: 'total_of_finished_orders_daily_rent', key: 'total_of_finished_orders_daily_rent'},
                        {title: '', dataIndex: 'order_origin_amount_daily_rent', key: 'order_origin_amount_daily_rent'}
                    ]
                },
                {
                    title: '-',
                    children: [
                        {title: '', dataIndex: 'total_of_finished_orders_hot_line', key: 'total_of_finished_orders_hot_line'},
                        {title: '', dataIndex: 'order_origin_amount_hot_line', key: 'order_origin_amount_hot_line'}
                    ]
                }
            ],
            tableHeader1: [
                {
                    title: '', dataIndex: 'start_time', key: 'start_time', fixed: 'left', width: 100,
                },
                {
                    title: 'ios',
                    children: [
                        {title: '', dataIndex: 'total_of_finished_orders_yidao_ios', key: 'total_of_finished_orders_yidao_ios'},
                        {title: '', dataIndex: 'order_origin_amount_yidao_ios', key: 'order_origin_amount_yidao_ios'}
                    ]
                },
                {
                    title: 'Android',
                    children: [
                        {title: '', dataIndex: 'total_of_finished_orders_yidao_android', key: 'total_of_finished_orders_yidao_android'},
                        {title: '', dataIndex: 'order_origin_amount_yidao_android', key: 'order_origin_amount_yidao_android'}
                    ]
                },
                {
                    title: '',
                    children: [
                        {title: '', dataIndex: 'total_of_finished_orders_web', key: 'total_of_finished_orders_web'},
                        {title: '', dataIndex: 'order_origin_amount_web', key: 'order_origin_amount_web'}
                    ]
                },
                {
                    title: '',
                    children: [
                        {title: '', dataIndex: 'total_of_finished_orders_mobile_web', key: 'total_of_finished_orders_mobile_web'},
                        {title: '', dataIndex: 'order_origin_amount_mobile_web', key: 'order_origin_amount_mobile_web'}
                    ]
                },
                {
                    title: 'CRM',
                    children: [
                        {title: '', dataIndex: 'total_of_finished_orders_crm', key: 'total_of_finished_orders_crm'},
                        {title: '', dataIndex: 'order_origin_amount_crm', key: 'order_origin_amount_crm'}
                    ]
                },
                {
                    title: '',
                    children: [
                        {title: '', dataIndex: 'total_of_finished_orders_open_platform', key: 'total_of_finished_orders_open_platform'},
                        {title: '', dataIndex: 'order_origin_amount_open_platform', key: 'order_origin_amount_open_platform'}
                    ]
                },
                {
                    title: '',
                    children: [
                        {title: '', dataIndex: 'total_of_finished_orders_web_open_platform', key: 'total_of_finished_orders_web_open_platform'},
                        {title: '', dataIndex: 'order_origin_amount_web_open_platform', key: 'order_origin_amount_web_open_platform'}
                    ]
                },
                {
                    title: 'H5',
                    children: [
                        {title: '', dataIndex: 'total_of_finished_orders_mobile_open_platform', key: 'total_of_finished_orders_mobile_open_platform'},
                        {title: '', dataIndex: 'order_origin_amount_mobile_open_platform', key: 'order_origin_amount_mobile_open_platform'}
                    ]
                },
                {
                    title: 'iPhone App',
                    children: [
                        {title: '', dataIndex: 'total_of_finished_orders_iphone', key: 'total_of_finished_orders_iphone'},
                        {title: '', dataIndex: 'order_origin_amount_iphone', key: 'order_origin_amount_iphone'}
                    ]
                },
                {
                    title: '',
                    children: [
                        {title: '', dataIndex: 'total_of_finished_orders_other', key: 'total_of_finished_orders_other'},
                        {title: '', dataIndex: 'order_origin_amount_other', key: 'order_origin_amount_other'}
                    ]
                }
            ],
            tableHeader2: [
                {
                    title: '', dataIndex: 'start_time', key: 'start_time'
                },
                {
                    title: '',
                    children: [
                        {title: '', dataIndex: 'total_of_finished_orders_diamond_card', key: 'total_of_finished_orders_diamond_card'},
                        {title: '', dataIndex: 'order_origin_amount_diamond_card', key: 'order_origin_amount_diamond_card'}
                    ]
                },
                {
                    title: '',
                    children: [
                        {title: '', dataIndex: 'total_of_finished_orders_white_gold_card', key: 'total_of_finished_orders_white_gold_card'},
                        {title: '', dataIndex: 'order_origin_amount_white_gold_card', key: 'order_origin_amount_white_gold_card'}
                    ]
                },
                {
                    title: '',
                    children: [
                        {title: '', dataIndex: 'total_of_finished_orders_golden_card', key: 'total_of_finished_orders_golden_card'},
                        {title: '', dataIndex: 'order_origin_amount_golden_card', key: 'order_origin_amount_golden_card'}
                    ]
                },
                {
                    title: '',
                    children: [
                        {title: '', dataIndex: 'total_of_finished_orders_silver_card', key: 'total_of_finished_orders_silver_card'},
                        {title: '', dataIndex: 'order_origin_amount_silver_card', key: 'order_origin_amount_silver_card'}
                    ]
                }
            ],
            tableHeader3: [
                {
                    title: '', dataIndex: 'start_time', key: 'start_time'
                },
                {
                    title: '',
                    children: [
                        {title: '', dataIndex: 'total_of_finished_orders_new', key: 'total_of_finished_orders_new'},
                        {title: '', dataIndex: 'order_origin_amount_new', key: 'order_origin_amount_new'}
                    ]
                },
                {
                    title: '',
                    children: [
                        {title: '', dataIndex: 'total_of_finished_orders_old', key: 'total_of_finished_orders_old'},
                        {title: '', dataIndex: 'order_origin_amount_old', key: 'order_origin_amount_old'}
                    ]
                }
            ],
            exportParams: {}
        }
    }
    componentWillMount() {
        this.initDateRange(this.state.dayNum);//
    }
    componentDidMount(){
        const params = {
            city: '',
            start_at: this.state.start_at,
            end_at: this.state.end_at, //n
            group_by: this.state.group_by
        }
        this.setState({
            load:true,
            tableHeader: this.state.tableHeader0
        },() => {
            this.getTableData(params);
        })
    }
    //
    initDateRange(rangeDays) {
        //moment
        const  endTime= moment().subtract(1, 'days');//
        const startTime = moment().subtract(rangeDays, 'days');//
        const start = new Date((moment(startTime).subtract())._d);
        const end = new Date((moment(endTime).subtract())._d);
        this.setState({
            city: '',
            start_at: this.formatDate(start),
            end_at: this.formatDate(end), //n
            car_type_id: ''
        });
    }
    // 
    initExportData() {
        const exportParams = {
            start_at: this.state.start_at,
            end_at: this.state.end_at,
            title: this.state.title,
            city: this.state.city,
            car_type_id: this.state.car_type_id,
            tableHeader: this.state.tableHeader,
            exportData: this.state.tableData
        }
        this.setState({
            exportParams: exportParams
        })
    }
    // 
    formatDate (date) {
        var y = date.getFullYear();
        var m = date.getMonth() + 1;
        m = m < 10 ? '0' + m : m;
        var d = date.getDate();
        d = d < 10 ? ('0' + d) : d;
        return y + '-' + m + '-' + d;
    };
    // 
    searchParams(params){

        this.setState({
            city: params.city,
            start_at: params.selectedStartDate,
            end_at: params.selectedEndDate
        })
    }
    // 
    carTypeChange(e) {
        let index = e.target.value;
        if(index === 'product_type_id'){
            this.setState({
                tableHeader: this.state.tableHeader0,
                xScroll: '100%'
            })
        }else if(index === 'order_source'){
            this.setState({
                tableHeader: this.state.tableHeader1,
                xScroll: '160%'
            })
        }else if(index === 'user_level_id'){
            this.setState({
                tableHeader: this.state.tableHeader2,
                xScroll: '100%'
            })
        }else if(index === 'user_active_type'){
            this.setState({
                tableHeader: this.state.tableHeader3,
                xScroll: '100%'
            })
        }
        this.setState({
            group_by: index
        },() => {
            this.searchBtn()
        })
    }
    // 
    searchBtn() {
        this.setState({
            load: true,
            total: this.getTotalPage()
        },() => {
            this.getTableData()
        })
    }
    // 
    pageChange(page, pageSize) {
        this.setState({
            current: page,
            pageSize: pageSize,
            load: true
        },() => {
            this.getTableData()
        })
    }
    onShowSizeChange(current, size) {
        this.setState({
            pageSize: size,
            current: current,
            load: true
        }, () => {
            this.getTableData();
        });
    }
    // 
    getTableData() {
        let arrStr = ['start_time']
        let searchParams = this.getParams();
        let result =getFun('/web_api/operation/order_dist',  searchParams);
        result.then(res => {
            this.setState({
                load: false,
                tableData: objectToArr(res.data, arrStr)

            },() => this.initExportData())
        }).catch(err => {
            console.log(err)
        })
    }
    // 
    getParams() {
        let start, end;
        start = this.pageStartDate().format("YYYY-MM-DD");
        end = this.pageEndDate().format("YYYY-MM-DD");
        const params = {
            start_at: start,
            end_at: end,
            city: this.state.city,
            group_by: this.state.group_by
        }
        return params;
    }
    //
    pageEndDate() {
        let days = (this.state.current - 1) * this.state.pageSize;
        let copy = moment(this.state.end_at).add(0, 'days'); //
        return  copy.subtract(days, 'days');
    }

    //
    pageStartDate() {
        let days = this.state.current * this.state.pageSize;
        let copy, dt;
        copy = moment(this.state.end_at); //
        copy = copy.add(1, 'days'); //+1
        dt = copy.subtract(days, 'days');
        if(this.state.start_at && dt< this.state.start_at) {
            return this.state.start_at;
        }
        return dt;
    }
    getTotalPage() {
        let day = dateDiff(this.state.start_at, this.state.end_at);
        return day;
    }
    render() {
        let {title, orderOptions, load, tableHeader, total, pageSize, xScroll, exportParams} = this.state;
        const radioChildren = Object.keys(orderOptions).map((key, index) => {
            return <RadioButton key={key} value={key}>{orderOptions[key]}</RadioButton>
        });
        let tableData = milliFormat(this.state.tableData);
        return (
            <div>
                <div className="operating-wrapper">
                    <Card title={title}  bordered={false}>

                        <Row gutter={16}>
                            <Col span={14}>
                                <div>
                                    <SearchBox searchParams={params => this.searchParams(params)}></SearchBox>
                                </div>
                                <div className="cartype-wrapper">
                                    <RadioGroup onChange={this.carTypeChange.bind(this)} defaultValue='product_type_id' >
                                        {radioChildren}
                                    </RadioGroup>
                                </div>
                            </Col>
                            <Col span={10}>
                                <Button type="primary" onClick={this.searchBtn.bind(this)}></Button>
                            </Col>
                        </Row>
                        <div>
                            <Table dataSource={tableData} bordered loading={load} columns={tableHeader} pagination={false} scroll={{x: xScroll}}>

                            </Table>
                        </div>
                        <div className="page-footer">
                            <Row>
                                <Col span={10}>
                                    <ExportFileCom params={exportParams}></ExportFileCom>
                                </Col>
                                <Col span={14} style={{textAlign: 'right'}}>
                                    <Pagination size="small" total={total} onChange={this.pageChange.bind(this)} pageSize={pageSize} showSizeChanger={true} onShowSizeChange={this.onShowSizeChange.bind(this)} showQuickJumper></Pagination>
                                </Col>
                            </Row>
                        </div>
                    </Card>
                </div>
            </div>
        )
    }
}
export default Distribution;


